export default {
    props: {
        item: {
            type: Object,
            default() {
                return {};
            }
        },
        isNew: {
            type: String|Number,
            default() {
                return "";
            }
        },
        // 是否点击进入详情
        isGoDetail: {
            type: Boolean,
            default() {
                return true;
            }
        },
    },

    data(){
        return{
            statusText:{
                // -3带看过期 -2:报备过期  -1:撤销  0:无效    1:报备 2:审核 3:带看  4:认购  5:签约  6:结佣  7:退佣   8.提交
                "-3": "带看过期",
                "1" : "待审核",
                "2" : "待确客",
                "3" : "待认购",
                "4" : "待签约",
                "5" : "待结佣",
                "6" : "已结佣",
                "7" : "已退佣",
                "-2": "已过期",
                "-1": "撤销认购",
                "0" : "无效",
            },
            statusClass:{
                "1" : "status_orange",
                "2" : "status_orange",
                "3" : "status_orange",
                "4" : "status_orange",
                "5" : "status_orange",
                "6" : "status_green",
                "7" : "status_gray",
                "-2": "status_gray",
                "-1": "status_gray",
                "0" : "status_gray",
                "-3": "status_gray",
            },
            directCustomerStatusText: {
                "3" : "待认购",
                "4" : "待签约",
                "5" : "已签约",
                "-1": "撤销认购",
            },
            directCustomerStatusClass: {
                "3" : "status_orange",
                "4" : "status_orange",
                "5" : "status_green",
                "-1": "status_gray",
            }
        }
    },

    methods : {
        goToCustomerDetailView(item){
            uni.navigateTo({
                url: '/pagesCustomer/customerDetail/customerDetail?custId='+ item.custId
                    + '&cityId=' + item.cityId + '&custName=' + item.custName
                    + "&isNew=" + this.isNew
            });
        }
    }
}
